.gradient-background {
    width: 100%;
    font-family: Montserrat;
    padding: 4rem 3rem;
    position: relative;
    background: linear-gradient(
      to bottom,
      #ffffff 0%,
      #b7e1ff49 30%,
      #b2c0ca4b 70%,
      #C6DEED0A 100%
    );
    /* background: linear-gradient(
      to bottom,
      #ffffff 0%,
      #b7e1ff49 30%,
